.pagination {
    margin: 0 auto 40px;
    list-style-type: none;
    display: flex;
    padding: 0;
}

.pagination li {
    cursor: pointer;
}

.pagination a {
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
}

.pagination a,
.pagination a.active,
.pagination li.active {
    border-radius: 5px;
}

.pagination a.active,
.pagination li.active a {
    background-color: #3f51b5;
    color: white !important;
}

.pagination a:hover:not(.active) {
    background-color: #ddd;
}

.pagination li:focus,
.pagination a:focus {
    outline: 0;
}

.posts-list {
    height: 500px;
    overflow-y: scroll;
}

/* 
 ANIMATIONS
 */
 .fade-out-dismiss-enter {
    opacity: 0.01;
  }
  
  .fade-out-dismiss-enter.example-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }
  
  .fade-out-dismiss-leave {
    opacity: 1;
  }
  
  .fade-out-dismiss-leave.example-leave-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
  }
